import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { localStoragedecryp,formatGram, getUserdata } from "../../../../../components/helpers/function";
class LaporanBarangSumaryExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let total_stock_awal = 0;
    let total_berat_awal = 0;
    let total_stock_in = 0;
    let total_berat_in = 0;
    let total_stock_out = 0;
    let total_berat_out = 0;
    let total_stock_hancur = 0;
    let total_berat_hancur = 0;
    let total_stock_jual = 0;
    let total_berat_jual = 0;
    let total_stock_akhir = 0;
    let total_berat_akhir = 0;
    let total_stock_tambah = 0;
    let total_berat_tambah = 0;
    let total_berat_selisih = 0;
    this.props.data.forEach((item, index) => {
      total_stock_awal = total_stock_awal + item.stock_awal;
      total_berat_awal = total_berat_awal + item.berat_awal;
      total_stock_tambah = total_stock_tambah + item.stock_tambah;
      total_berat_tambah = total_berat_tambah + item.berat_tambah;
      total_stock_jual = total_stock_jual + item.stock_jual;
      total_berat_jual = total_berat_jual + item.berat_jual;
      total_stock_hancur = total_stock_hancur + item.stock_hancur;
      total_berat_hancur = total_berat_hancur + item.berat_hancur;
      total_stock_akhir = total_stock_akhir + item.stock_akhir;
      total_berat_akhir = total_berat_akhir + item.berat_akhir;
      total_stock_in = total_stock_in + item.stock_in;
      total_stock_out = total_stock_out + item.stock_out;
      total_berat_in = total_berat_in + item.berat_in;
      total_berat_out = total_berat_out + item.berat_out;
      total_berat_selisih = total_berat_selisih + item.berat_selisih_jual;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Barang Summary"
          sheet="Laporan Barang Summary"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="16" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN BARANG TOKO{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="16" style={{ textAlign: "center" }}>
                {" "}
                Tanggal{" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tanggal}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="16"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="16"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              {
            /* {
        content: `Baki`,
    
      {
        content: `Hancur Ptg`,
      },
      {
        content: `Hancur Berat`,
      },

      {
        content: `Ptg Akhir`,
      },
      {
        content: `Berat Akhir`,
      },
      {
        content: `Stock In`,
      },
      {
        content: `Stock Out`,
      },
      {
        content: `Berat In`,
      },
      {
        content: `Berat Out`,
      }, */}
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BAKI
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                Potong Awal
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                BERAT AWAL
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                TAMBAH PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                TAMBAH BERAT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                JUAL PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                BERAT JUAL
              </th>

              
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                HANCUR PTG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                HANCUR BERAT
              </th>
         
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                PTG AKHIR
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                BERAT AKHIR
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                STOCK IN
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                STOCK OUT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                BERAT IN
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                BERAT OUT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" ,textAlign: "right"}}>
                BERAT SELISIH PENJUALAN
              </th>

              
            </tr>
          </thead>

          <tbody>
          {this.props.data.map((item, index) => (
                <tr key={index}>
                  <td style={{textAlign: "center"}}>{item._id.kode_toko}</td>
                  <td style={{textAlign: "right"}}>{item.stock_awal}</td>
                  <td style={{textAlign: "right"}}>{formatGram(item.berat_awal,3)}</td>
                  <td style={{textAlign: "right"}}>{item.stock_tambah}</td>
                  <td style={{textAlign: "right"}}>{formatGram(item.berat_tambah,3)}</td>
                  <td style={{textAlign: "right"}}>{item.stock_jual}</td>
                  <td style={{textAlign: "right"}}>{formatGram(item.berat_jual,3)}</td>
                  <td style={{textAlign: "right"}}>{item.stock_hancur}</td>
                  <td style={{textAlign: "right"}}>{formatGram(item.berat_hancur,3)}</td>
                  <td style={{textAlign: "right"}}>{item.stock_akhir}</td>
                  <td style={{textAlign: "right"}}>{formatGram(item.berat_akhir,3)}</td>
                  <td style={{textAlign: "right"}}>{item.stock_in}</td>
                  <td style={{textAlign: "right"}}>{item.stock_out}</td>
                  <td style={{textAlign: "right"}}>{formatGram(item.berat_in,3)}</td>
                  <td style={{textAlign: "right"}}>{item.berat_out.toFixed(3)}</td>
                  <td style={{textAlign: "right"}}>{formatGram(item.berat_selisih_jual,3)}</td>
                </tr>
          ))}
          </tbody>
          <tfoot>
            <tr>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                Total
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                {total_stock_awal}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                {total_berat_awal}{" "}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{total_stock_tambah}</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{formatGram(total_berat_tambah,3)}</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{total_stock_jual }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{formatGram(total_berat_jual,3) }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{total_stock_hancur }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{formatGram(total_berat_hancur,3) }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{total_stock_akhir }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{formatGram(total_berat_akhir,3) }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{total_stock_in }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{total_stock_out }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{formatGram(total_berat_in,3) }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{formatGram(total_berat_out,3) }</td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>{formatGram(total_berat_selisih,3) }</td>
             
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanBarangSumaryExel;
